import { useState, useEffect } from "react";
import "../css/navbar.css";

const Navbar = () => {
    const [toggle, setToggle] = useState("home")

    const navbar = {navbars : [
        {name: "home", status: "active"},
        {name: "skills", status: null},
        {name: "experience", status: null},
        {name: "portfolio", status: null},
        {name: "contact", status: null}
    ]}
    const [navbars, setNavbars] = useState(navbar.navbars)

    useEffect(() => {
        let navbar = [];
        navbars.forEach(nav => {
            if(nav.name === toggle){
                nav.status = "active"
                navbar.push(nav)
            }
            else{
                nav.status = null
                navbar.push(nav)
            }
            
        });
        setNavbars(navbar)
    }, [toggle])

    return(
        <div id="navbar" className="col-12 px-0">
            <nav className="navbar navbar-expand-lg navbar-light bg-white px-lg-5">
                <a className="navbar-brand logo" href="#!"><h1 className="m-0 p-0 text-center">M</h1></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#!navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav ml-auto">
                        {
                            navbars.map((nav, i) => (
                                <li key={i} onClick={() => (setToggle(`${nav.name}`))} className={`nav-item ${nav.status}`}>
                                        <a className="nav-link" href={`#${(nav.name === "home") ? "" : nav.name}`}>{nav.name.toUpperCase()}</a>
                                    </li>
                            ))
                        }
                    </ul>
                </div>
            </nav>
            
        </div>
    )
}

export default Navbar;