import React from "react";
import { Link } from "react-router-dom";
const View = (props) => {
    document.body.style.overflow = "hidden"
    return(
        <div id="view">
            <div className="row h-100 justify-content-center">
                <h1 className="text-center col-12 m-0"><Link to="/" onClick={() => (document.body.style.overflow = "auto")}>&times;</Link></h1>
                <div className="col-12 text-white">
                    <img className="view-img" src={props.logo} alt="zaqatalapfk" />
                </div>
            </div>
        </div>
    )
}

export default View;