
const Skill = (props) =>{
    return(
        <div className="skill col-12 mt-4" align="center">
            <div className="row text-light" align="left">
                <p className="align-self-center">{props.name}</p>
                <p className="align-self-center" style={{width: props.value}}></p>
                <p className="align-self-center">{props.interest}</p>
            </div>
        </div>
    )
}

export default Skill;