import { Component } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import "../css/portfolio.css";
import logo_1 from "../img/zaqatalapfk.png";
import View from "./View"



class Portfolio extends Component{
    
    state = {logo: ""}
    render(){
        return(
            <div id="portfolio" className="col-12 w-100 position-relative">
                <div className="transparent"></div>

                <div className="row justify-content-center">
                    <Router>
                        <h1 className="text-center w-100">PORTFOLIO</h1>
                        <div className="columns py-5">
                            <Link to="/view"><img className="portfolio-img" onClick={() => this.setState({logo: logo_1})} src={logo_1} alt="zaqatalapfk" /></Link>
                        </div>
                        <Switch>
                            <Route path="/view">
                                <View logo={this.state.logo}/>
                            </Route>
                        </Switch>
                    </Router>
                    
                </div>
            </div>
        )
    }
}

export default Portfolio