import Skill from "./Skill"
import { Component } from "react";
import "../css/skills.css";

class Skills extends Component{
    state = {skills: [
        {name: "WORD", value: "63%", interest: "90%"},
        {name: "EXCEL", value: "49%", interest: "70%"},
        {name: "POWERPOINT", value: "63%", interest: "90%"},
        {name: "HTML & CSS", value: "63%", interest: "90%"},
        {name: "Bootstrap 4", value: "56%", interest: "80%"},
        {name: "JavaScript", value: "52.5%", interest: "75%"},
        {name: "Jquery", value: "59.5%", interest: "85%"},
        {name: "React", value: "45.5%", interest: "65%"},
        {name: "PHP", value: "42%", interest: "60%"},
        {name: "MySQL", value: "49%", interest: "70%"},
    ]}
    render(){
        return(
            <div id="skills" className="col-12 w-100 position-relative">
                <div className="transparent"></div>

                <div className="skills-div row justify-content-center">
                    <h1 className="w-100 text-center mb-3">SKILLS</h1>
                    <h2 className="w-100 text-center mb-5">MY KNOWLEDGE LEVEL IN SOFTWARE</h2>
                    {
                        this.state.skills.map((skill, i) => {
                            return(
                                <Skill key={i} name={skill.name} value={skill.value} interest={skill.interest}/>
                            )
                        })
                    }
                    
                </div>
            </div>
        )
    }
}

export default Skills;